import wPicker from "@/components/w-picker/w-picker.vue";
import uCharts from '../../../js_sdk/u-charts/u-charts/u-charts.js';
var _self;
var canvaColumn = null;
export default{
	components: {
		wPicker
	},
	data(){
		return{
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			serverData: '',
			total: 0,
			date: '',
			list: [],
			activeIndex: 0,
			tabList: ["明细", "图表"]
		}
	},
	onLoad() {
		_self = this;
		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		let currentDate = new Date();
		let year = currentDate.getFullYear();
		let month = currentDate.getMonth() + 1;
		if (month < 10) {
			month = '0' + month;
		}
		let nowDate = year + '-' + month;
		this.date = nowDate;
		this.getServerData();
	},
	onShow() {
		this.getServerData();
	},
	methods: {
		getServerData() {
			this.$http.get('/petbnb/getAccountList', {
					date: this.date || nowDate
				})
				.then(res => {
					this.list = res.data.list;
					for (let i of res.data.list) {
						i.data = parseInt(i.cost);
						i.icon = this.$API + "/image/account_" + `${i.type_id}` + ".png"
					}
					let Ring = {
						series: []
					};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					if (this.list.length == 0) {
						this.total = 0;
						Ring.series = [{
							color: 'skyblue',
							data: 0,
							name: '暂无'
						}]
					} else {
						this.total = res.data.sum;
						Ring.series = res.data.list;
					}
					_self.showRing("canvasRing", Ring);
				})
		},
		showRing(canvasId, chartData) {
			let canvaRing = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'ring',
				fontSize: 11,
				legend: true,
				title: {
					name: '总支出',
					color: '#7cb5ec',
					fontSize: 20 * _self.pixelRatio,
					offsetY: -10 * _self.pixelRatio,
				},
				subtitle: {
					name: '￥' + _self.total,
					color: '#666666',
					fontSize: 15 * _self.pixelRatio,
					offsetY: 10 * _self.pixelRatio,
				},
				extra: {
					pie: {
						offsetAngle: -45,
						ringWidth: 20 * _self.pixelRatio,
						labelWidth: 15
					}
				},
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				disablePieStroke: true,
				dataLabel: true,
			});
		},
		touchRing(e) {
			canvaRing.showToolTip(e, {
				format: function(item) {
					return item.name + ':' + item.data
				}
			});
		},
		showPicker() {
			this.$refs.picker.show();
		},
		pickTime(e) {
			this.date = e.result;
			this.getServerData();
		}
	}
}
